import './Cart.scss';
import React from 'react';
import { Button, Table } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addCount, sortName, minusCount, deleteCount, deleteAll } from '../store';
import { useNavigate } from 'react-router-dom';

const Cart = () => {
  let cart = useSelector(state => state.cart);
  let dispatch = useDispatch();
  const navigate = useNavigate()
  let a = <tr className='nolistbox'>
    <td colSpan={8}>
      <h3>장바구니에 담긴 상품이 없습니다 :(</h3>
      <p>여러분의 말차로 장바구니를 채워보세요!</p>
      <button onClick={() => navigate('/product/all')}>쇼핑 계속하기 +</button>
    </td>
  </tr>;
  let b = cart.map((v, i) =>
    <tr key={i}>
      <td>{cart.length}</td>
      <td>{v.item}</td>
      <td>{v.amount}</td>
      <td>x</td>
      <td>{(v.price).toLocaleString()}</td>
      <td>=</td>
      <td className='ttp'>{(v.totalPrice).toLocaleString()}</td>
      <td className='but'>
        <button onClick={() =>
          dispatch(addCount(i))
        } className='plus'>+</button>

        <button onClick={() =>
          dispatch(minusCount(i))
        } className='minus'>-</button>

        <button onClick={() => {
          dispatch(deleteCount(i))
        }} className='del'>삭제</button>
      </td>
    </tr>
  );
  let result = cart.length === 0 ? a : b;

  const total = cart.reduce((acc, item) => acc + (item.totalPrice || 0), 0);

  let shippingprice = (total > 50000 || cart.length === 0) ? 0 : 3000;

  return (
    <div className='cartwrap'>
      <div className='top'>
        <Table className='uptable'>
          <thead>
            <tr>
              <th className='space'></th>
              <th className='space3'>상품명</th>
              <th className='space2'>수량</th>
              <th className='space'></th>
              <th className='space2'>가격(수량 1개)</th>
              <th className='space'></th>
              <th>총 가격</th>
              <th>변경하기</th>
            </tr>
          </thead>
          <tbody>
            {result}
          </tbody>
        </Table>
        
        <div className="buttonlist">
          <Button variant="outline-dark" onClick={() => {
            dispatch(sortName())
          }}>이름순 정렬하기</Button>
          <Button variant="outline-dark" onClick={() => dispatch(deleteAll())}>전체 삭제하기</Button>
          <Button variant="outline-dark" onClick={() => navigate('/product/all')}>이어서 쇼핑하기</Button>
        </div>


        <div className='downtablewrap'>
          <Table className='downtable'>
            <thead>
              <tr>
                <th>선택상품금액</th>
                <th></th>
                <th>배송비</th>
                <th></th>
                <th>주문금액</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{total.toLocaleString()}원</td>
                <td>+</td>
                <td>{shippingprice}원</td>
                <td>=</td>
                <td className='ttp'>총 {(total + shippingprice).toLocaleString()}원</td>
              </tr>
            </tbody>
            
          </Table>
          <button>
            바로 주문하기
          </button>
        </div>
        <p className='small'>*총 주문금액이 5만원이상이면 배송비 무료입니다.</p>

      </div>

      <div className='listtext'>
        <ul>
          <li>장바구니에 최대 200개의 상품을 담을 수 있습니다.
          </li>
          <li>장바구니 상품은 최대 90일간 저장됩니다.
          </li>
          <li>장바구니에 담긴 상품은 옵션 또는 추가상품 단위로 최대 100개의 상품만 동시 주문할 수 있습니다.
          </li>
          <li>가격, 옵션 등 정보가 변경된 경우 주문이 불가할 수 있습니다.
          </li>
          <li>오늘출발 정보는 판매자가 설정한 정보에 의해 제공되며, 물류위탁 상품인 경우 물류사의 사정에 따라 실제와 다를 수 있습니다.
          </li>
          <li>일부 상품의 경우 카드 할부기간이 카드사 제공 기간보다 적게 제공될 수 있습니다.
          </li>
          <li>장바구니에 보여지는 배송비는 최종적으로 발생하는 배송비가 아니며, 주문 주소지에 따라 도서산간 비용 등이 추가 발생할 수 있습니다.
          </li>
        </ul>
      </div>




    </div>
  )
}

export default Cart