import React from 'react'
import { Col, Row } from 'react-bootstrap';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import 'swiper/scss/scrollbar';
import './Home.scss'

// import required modules
import { Pagination, Navigation, Autoplay, Scrollbar } from 'swiper/modules';
import { useNavigate } from 'react-router-dom';

const Home = () => {
    const navigate = useNavigate();

    return (
        <>

            <Swiper
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                loop={true}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper"
            >
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/s1.jpg'} alt="slide1" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/s2.jpg'} alt="slide2" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/s3.jpg'} alt="slide3" /></SwiperSlide>
            </Swiper>

            <div className='wrap'>
                <button className='first_b'>오직 슈퍼말차에서만</button>
            </div>
            <Swiper
                scrollbar={{
                    hide: false,
                }}
                slidesPerView={4}
                modules={[Scrollbar]}
                className="miniSwiper"
                breakpoints={{
                    320: {
                        slidesPerView: 1.5,
                        // spaceBetween: 10,
                    },
                    480: {
                        slidesPerView: 1.5,
                        // spaceBetween: 12,
                    },
                    768: {
                        slidesPerView: 3,
                        // spaceBetween: 14,
                    },
                    1260:{
                        slidesPerView:3
                    },
                    1735: {
                        slidesPerView: 3,
                        // spaceBetween: 16,
                    },
                    1920:{
                        slidesPerView: 4
                    }
                }}
            >
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini1.png'} alt="mini1" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini2.png'} alt="mini2" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini3.png'} alt="mini3" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini4.png'} alt="mini4" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini5.png'} alt="mini5" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini6.png'} alt="mini6" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini7.png'} alt="mini7" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini8.png'} alt="mini8" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini9.png'} alt="mini9" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini10.png'} alt="mini10" /></SwiperSlide>
                <SwiperSlide className='slide_part'><img src={process.env.PUBLIC_URL + '/img/mini11.png'} alt="mini11" /></SwiperSlide>
            </Swiper>

            <div className='touch'>
                <Row className='g-0'>
                    <Col md={6} onClick={() => {
                        navigate('/product/dessert')
                    }}><img src={process.env.PUBLIC_URL + '/img/d1.png'} alt="detail" /></Col>
                    <Col md={6} onClick={() => {
                        navigate('/product/goods')
                    }}><img src={process.env.PUBLIC_URL + '/img/d2.png'} alt="detail" /></Col>
                </Row>

                <Row className='g-0'>
                    <Col md={6} onClick={() => {
                        navigate('/product/supermatcha')
                    }}><img src={process.env.PUBLIC_URL + '/img/d3.png'} alt="detail" /></Col>
                    <Col md={6} onClick={() => {
                        navigate('/product/superbori')
                    }}><img src={process.env.PUBLIC_URL + '/img/d4.png'} alt="detail" /></Col>
                </Row>
            </div>
        </>
    )
}

export default Home