let cart = createSlice({
    name: 'cart',
    initialState: [
    ],
    reducers: {
        addCount(state, action) {
            let idx = action.payload;
            ++state[idx].amount;
            state[idx].totalPrice = state[idx].amount * state[idx].price;
        },
        sortName(state) {
            state.sort((a, b) => a.item > b.item ? 1 : -1,
            );

        },
        addItem(state, action) {
            const item = { ...action.payload };
            item.totalPrice = item.amount * item.price;
            state.push(item);
        },
        minusCount(state, action) {
            let minus = action.payload;
            if (state[minus].amount > 1) {
                --state[minus].amount;
                state[minus].totalPrice = state[minus].amount * state[minus].price;
            } else {
                alert('더 이상 줄일 수 없어요.');
                state[minus].amount = 1;
                state[minus].totalPrice = state[minus].amount * state[minus].price;
            }
        },
        deleteCount(state, action) {
            let idx = action.payload;
            state.splice(idx, 1);
        },
        deleteAll(state) {
            return [];
        }
    }
})
export let { addCount, sortName, addItem, minusCount, deleteCount, deleteAll } = cart.actions



