import './Menu.scss';
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'

const Menu = () => {
    const menulist = useSelector(state => state.menulist)
    const navigate = useNavigate()
    let { Mcate } = useParams()
    const Mcategory = Mcate === 'all' ? menulist : menulist.filter((v, i) => v.part === Mcate)

    const [showModal, setShowModal] = useState(false);
    const [selectedItem, setSelectedItem] = useState(null);
    return (

        <div className='menuwrap'>
            <div className='contentwrap'>
                <div className='tab'>
                    <ul className='tablist'>
                        <li onClick={() => {
                            navigate('/menu/all')
                        }}>전체</li>
                        <li onClick={() => {
                            navigate('/menu/sweet')
                        }}>SWEET</li>
                        <li onClick={() => {
                            navigate('/menu/pure')
                        }}>PURE</li>
                        <li onClick={() => {
                            navigate('/menu/oatcream')
                        }}>OAT CREAM</li>
                        <li onClick={() => {
                            navigate('/menu/crush')
                        }}>CRUSH</li>
                        <li onClick={() => {
                            navigate('/menu/icedessert')
                        }}>ICE DESSERT</li>
                        <li onClick={() => {
                            navigate('/menu/coffee')
                        }}>COFFEE</li>
                    </ul>

                    <div className='sym1'>
                        <img src={process.env.PUBLIC_URL + "/img/symbol.svg"} alt="symbol" />
                    </div>
                    <div className='sym2'>
                        <img src={process.env.PUBLIC_URL + "/img/symbol_circle.svg"} alt="symbol_circle" />
                    </div>
                </div>

                <div className='box'>
                    {
                        Mcategory.map((v, i) =>
                            <div key={i} className='boxinner'>
                                <div className='biimg'>
                                    <img src={process.env.PUBLIC_URL + v.img} alt='boximg' />
                                </div>
                                <div className='bi_1'>
                                    <span>{v.id}</span>
                                    <p>I'm the {v.part}</p>
                                </div>

                                <h5>{v.item}</h5>

                                <div className='bi_2'>
                                    <div className="pricelist">
                                        <h6>{
                                            (
                                                () => {
                                                    const priceArr = Object.keys(menulist[i]).filter((key) => key.indexOf('price') >= 0)

                                                    if (!menulist) {
                                                        return <div>불러오는 중...</div>; // 또는 null 반환
                                                    } else if (priceArr.length === 1 && priceArr[0] === 'price') {
                                                        return <div>{menulist[i].price.toLocaleString()}원</div>;
                                                    } else if (priceArr.length === 2) {
                                                        return (
                                                            <div>
                                                                R {menulist[i].r_price.toLocaleString()}원 / L {menulist[i].l_price.toLocaleString()}원
                                                            </div>
                                                        );
                                                    } else if (priceArr.length === 1 && priceArr[0] === 'r_price') {
                                                        return <div>R {menulist[i].r_price.toLocaleString()}원</div>;
                                                    } else if (priceArr.length === 1 && priceArr[0] === 'l_price') {
                                                        return <div>L {menulist[i].l_price.toLocaleString()}원</div>;
                                                    } else {
                                                        return <div>가격 정보 없음</div>;
                                                    }
                                                }
                                            )()
                                        }
                                        </h6>
                                    </div>

                                    <div className='n_button'>
                                        <button className='n_btn_1' onClick={() => {
                                            setSelectedItem(v);
                                            setShowModal(true)
                                        }}>영양 성분 보기</button>


                                    </div>

                                </div>
                            </div>
                        )
                    }
                    {showModal && selectedItem && (
                        <div className="nutriboxwrap">
                            <div className="nutribox">
                                <div className='nutriboxinner'>
                                    <img src={process.env.PUBLIC_URL + selectedItem.img} alt="mainimg" />
                                    <div className="nbox1">
                                        <h4>{selectedItem.item}</h4>
                                        <p>{selectedItem.memo}</p>

                                        <div className="nbox2">
                                            <span><b>열량</b> {selectedItem.kcal}</span>
                                            <span><b>나트륨</b> {selectedItem.Na}</span>
                                            <span><b>당류</b> {selectedItem.sweet}</span>
                                            <span><b>포화지방</b> {selectedItem.fat}</span>
                                            <span><b>단백질</b> {selectedItem.protein}</span>
                                            <span className='Gre'><b>카페인</b> {selectedItem.caffe}</span>
                                        </div>

                                        <button className='nbox1btn' onClick={() => setShowModal(false)}>닫기</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div >
    )
}

export default Menu