import React, { useRef, useState } from 'react'
import './Detail.scss'
import 'swiper/css';
import 'swiper/css/pagination';
import { Mousewheel, Pagination } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { addItem } from '../store'
import { useNavigate } from 'react-router-dom';
// import product from '../store';

const Detail = () => {
    let { id } = useParams();
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const product = useSelector(state => state.product)
    // const selproduct = product.filter(v=>v.id == id)

    let [count, setCount] = useState(1)
    const [bagOption, setBagOption] = useState("nope");
    const bagPrice = bagOption === "yes" ? 500 : 0;
    const totalPrice = (count * product[id-1].price) + bagPrice;

    const section1Ref = useRef(null); // img[0]
    const section2Ref = useRef(null); // img[1]
    const commentRef = useRef(null);  // 댓글 div

    const handleScroll = (ref) => {
        ref.current?.scrollIntoView({ behavior: "smooth" });
    };
    const [showAllReviews, setShowAllReviews] = useState(false);
    const reviews = product[id-1].reviews;
    const visibleReviews = showAllReviews ? reviews : reviews.slice(0, 5);
    const renderStars = (rating) => {
        const fullStars = '★'.repeat(rating);
        const emptyStars = '☆'.repeat(5 - rating);
        return fullStars + emptyStars;
    };
    return (
        <div>

            <div className='detailbox'>
                <div className='detailboxinner'>

                    <div className="left">
                        <Swiper
                            direction={'vertical'}
                            slidesPerView={1}
                            spaceBetween={0}
                            mousewheel={true}
                            pagination={{
                                clickable: true,
                            }}
                            modules={[Mousewheel, Pagination]}
                            className="mySwiper detailswiper">
                            <SwiperSlide><img src={process.env.PUBLIC_URL + product[id-1].imgsub1} alt="detailimgage" /></SwiperSlide>
                            <SwiperSlide><img src={process.env.PUBLIC_URL + product[id-1].imgsub2} alt="detailimgage" /></SwiperSlide>
                        </Swiper>

                        <nav>
                            <ul>
                                <li onClick={() => handleScroll(section1Ref)}>상품상세</li>
                                <li onClick={() => handleScroll(section2Ref)}>상품고시정보</li>
                                <li onClick={() => handleScroll(commentRef)}>고객리뷰</li>
                            </ul>
                        </nav>
                        
                        <div className="detailbottom">
                            <img ref={section1Ref} src={process.env.PUBLIC_URL + product[id-1].under_img[0]} alt="detailunderimg" />
                            <img ref={section2Ref} src={process.env.PUBLIC_URL + product[id-1].under_img[1]} alt="detailunderimg" />
                        </div>
                    </div>


                    <div className='detailT' >
                        <div className="realT">
                            <h3>{product[id-1].item}</h3>
                            <h5>{product[id-1].price.toLocaleString()}원</h5>
                            <p>{product[id-1].memo}</p>
                        </div>

                        <hr />
                        <div className="benefit">
                            <table>
                                <tbody>
                                    <tr>
                                        <td className='ttc'>혜택안내</td>
                                        <td>말차포인트 {product[id-1].price * 0.01}P 적립</td>
                                    </tr>
                                    <tr>
                                        <td className='ttc'>배송비</td>
                                        <td>3000원 (5만원 이상 무료배송)</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>

                        <hr />

                        <div className="sell">
                            <div className="sellamount">
                                <h3>구매수량</h3>

                                <div className="butp">
                                    <button onClick={() => { if (count > 1) setCount(count - 1); }
                                    }>-</button>
                                    <p>{count}</p>
                                    <button onClick={() =>
                                        setCount(++count)
                                    }>+</button>
                                </div>

                            </div>
                            <div className="bag">
                                <h3>쇼핑백</h3>
                                <select name="choose" defaultValue="" id="detailchoose" onChange={(e) => setBagOption(e.target.value)}>
                                    <option value="" disabled hidden>쇼핑백 선택</option>
                                    <option value="yes">쇼핑백 추가(+500원)</option>
                                    <option value="nope">선택 안함</option>
                                </select>
                            </div>

                            <div className="totalP">
                                <h3>상품 금액 합계</h3>
                                <p>{totalPrice.toLocaleString()}원</p>
                            </div>
                        </div>

                        <div className="buttonL">
                            <button>구매하기</button>
                            <button onClick={() => {
                                dispatch(addItem({ id: product[id-1].id, item: product[id-1].item, price: totalPrice, amount: count }));
                                navigate('/cart');
                            }}>장바구니</button>
                        </div>

                    </div>
                </div>

                <div ref={commentRef} className='review'>
                    <h3>고객 리뷰</h3>

                    <div className="rtop">
                        <h5>{product[id-1].item}의 생생한 후기</h5>
                        <h5>평균 {
                            (product[id-1].reviews.reduce((acc, cur) => acc + cur.userrate, 0) / product[id-1].reviews.length).toFixed(1)
                        }점</h5>
                    </div>

                    <div className="review-list">
                        {visibleReviews.map((review, i) => (
                            <div key={i} className="RLinner">
                                <div className="RLinnerup">
                                    <div className="Ruser">구매자 {review.user}</div>
                                    <div className="Rrate">{renderStars(review.userrate)}</div>
                                </div>
                                <div className="Rdate">{review.date}</div>
                                <div className="Ring">{review.reviewing}</div>
                            </div>
                        ))}
                    </div>
                    {reviews.length > 5 && (
                        <button
                            className="Rbutton"
                            onClick={() => setShowAllReviews(!showAllReviews)}
                        >
                            {showAllReviews ? "접기" : "더보기"}
                        </button>
                    )}
                </div>
            </div>
        </div>


    )
}

export default Detail