import './Product.scss';
import { useSelector, useDispatch } from 'react-redux'
import { addItem } from '../store'
import { useNavigate, useParams } from 'react-router-dom'

const Product = () => {
    const navigate = useNavigate()
    let { cate } = useParams()
    const dispatch = useDispatch()
    const product = useSelector(state => state.product)
    const category = cate === 'all' ? product : product.filter((v, i) => v.part === cate)
    return (
        <div className='contentwrap'>
            <div className='tab'>
                <ul className='tablist'>
                    <li onClick={() => {
                        navigate('/product/all')
                    }}>전체</li>
                    <li onClick={() => {
                        navigate('/product/dessert')
                    }}>디저트</li>
                    <li onClick={() => {
                        navigate('/product/goods')
                    }}>굿즈</li>
                    <li onClick={() => {
                        navigate('/product/supermatcha')
                    }}>슈퍼말차</li>
                    <li onClick={() => {
                        navigate('/product/superbori')
                    }}>슈퍼보리</li>
                </ul>

                <div className='sym1'>
                    <img src={process.env.PUBLIC_URL + "/img/symbol.svg"} alt="symbol" />
                </div>
                <div className='sym2'>
                    <img src={process.env.PUBLIC_URL + "/img/symbol_circle.svg"} alt="symbol_circle" />
                </div>
            </div>

            <div className='box'>
                {
                    category.map((v, i) =>
                        <div key={i} className='boxinner'>
                            <div className='biimg' onClick={()=>{
                                        navigate('/detail/'+ v.id)
                                    }}>
                                <img src={process.env.PUBLIC_URL + v.img} alt='boximg' />
                            </div>
                            <div className='bi_1'>
                                <span>{v.id}</span>
                                <p>I'm the {v.part}</p>
                            </div>

                            <h5>{v.item}</h5>

                            <div className='bi_2'>
                                <h6>{(v.price).toLocaleString()}원</h6>
                                <div>
                                    <button onClick={()=>{
                                        navigate('/detail/'+v.id)
                                    }}>구매</button>
                                    <button onClick={() => {
                                        dispatch(addItem({ id: v.id, item: v.item, price: v.price, amount: 1 }));
                                        navigate('/cart');
                                    }}>장바구니</button>
                                </div>

                            </div>


                        </div>
                    )
                }
            </div>
        </div>
    )
}

export default Product